.dial {
  position: relative;
  margin: 30px auto 0;
  height: 500px;
  width: 500px;
  border: 3px solid white;
  border-radius: 50%;
}

.point {
  position: absolute;
  height: 50%;
  width: 3px;
  left: 50%;
  transform-origin: 50% 100%;
}

.point .number {
  font-size: 28px;
  font-weight: bold;
  position: absolute;
}

.point .mark {
  background-color: white;
  width: 100%;
  height: 7%;
}

.hours,
.minutes,
.seconds {
  transition: all 0.3s;
  position: absolute;
  transform-origin: 50% 100%;
  left: 50%;
  bottom: 50%;
  background-color: white;
}

.hours {
  width: 4px;
  height: 33%;
}

.minutes {
  width: 4px;
  height: 43%;
}

.seconds {
  width: 2px;
  height: 43%;
}

.circle {
  position: absolute;
  width: 4%;
  height: 4%;
  background-color: white;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
